import {Component} from "react";
import { getPreviousDate, getToday, getYesterday } from "../utils/Date";
import Post from "./PostComponent";

class Home extends Component{
    state = {
        load: 2
    }
    componentDidMount(){
    }
    render(){
        const today = []
        const yesterday = []
        const earlier = []
        const data = this.props.data
        data.map((item) => {
            if(item.date === getToday()){
                today.push(item)
            }
            else if (item.date === getYesterday()){
                yesterday.push(item)
            }
            else{
                earlier.push(item)
            }
        })
        return(
            <div className="home-body">
                {today.length > 0 && <h5>Today's Posts</h5>}
                {today.slice(0,5).map((post) => {
                    return (
                        <Post 
                            data = {post}
                        />
                    )
                })}
                {yesterday.length > 0 && <h5>Yesterday's Posts</h5>}
                {yesterday.slice(0,5).map((post) => {
                    return (
                        <Post 
                            data = {post}
                        />
                    )
                })}
                {earlier.length > 0 && <h5>Earlier Posts</h5>}
                {earlier.slice(0,5).map((post) => {
                    return (
                        <Post 
                            data = {post}
                        />
                    )
                })}
          </div>
        )
    }
}

export default Home