import {Component} from "react";
import { Link } from "react-router-dom";
import '../styles/post.css'
class Post extends Component{
    render(){
        const { data } = this.props
        const profileLink = `/user/${data.user}/`
        return(
            <div className="post">
                <div className="post-header">
                    <Link to={profileLink} className="post-user"><h3 >{data.user}</h3></Link>
                    <h5 className="post-date">{data.date}</h5>                    
                </div>
                <section >
                    <a>
                        {data.type === "video" ? 
                        <iframe className="post-image"  src={data.url}>{}</iframe>:
                        <div className="post-image" style={{backgroundImage: `url(${data.url})`}}></div>
                        }
                    </a>
                </section>
                <section className="post-footer">
                    <h5 className="post-subtitle">0 Likes</h5>
                    <span className="post-description">
                        <p>{data.desc.slice(0,250)+(data.desc.length > 250 && "...")}</p>
                        <Link className="post-view-more" to={profileLink+data.id}>View Post</Link></span>
                </section>
            </div>            
        )
    }
}
export default Post