import { Component } from "react";
import { Link } from "react-router-dom";
import '../styles/page.css'
class PostPage extends Component{
    render(){
        const user = this.props.user
        const data = this.props.post
        return(
            data.map((post) =>             
            <div className="page-body">
                <img className="page-img" style={{backgroundImage: `url(${post.url})`}}></img>
                <div className="page-info">
                    <Link to={"/user/"+user} ><h3 className="page-text">{user}</h3></Link>
                    <h2 className="page-text">{post.title}</h2>
                    <h4 className="page-text">{post.date}</h4>
                    <h5>0 Likes</h5>
                    <div className="page-desc">{post.desc}</div>                    
                </div>
            </div>)
        )
    }
}

export default PostPage