import { Component } from 'react';
import { Switch,Route } from 'react-router';
import './styles/App.css';
import './styles/footer.css'
import './imgs/default.jpg';
import { startLoading,finishedLoading } from './utils/uxFunctions';
import * as NasaAPI from './utils/NasaAPI'
import Home from './Components/HomeComponent';
import Header from './Components/HeaderComponent';
import { PostLoader } from './Components/PostLoaderComponent';
//import search component
//import About component
import Footer from './Components/FooterComponent';
import { User } from './Components/User';

class App extends Component{
  state = {
    data: []
  }
  async getData(){
    const res = await NasaAPI.getCuriosity()
    this.setState({
      data: res
    })
    finishedLoading()
  }
  componentDidMount(){
    this.getData()
    startLoading("main")
  }
  render(){
    return(
      <div className="app">
        <Header />
        <main id="main">
            <Route exact path='/' render={() =>
            <Home data={this.state.data}/>
            }/>
            <Route exact path='/search' render={() =>
            <h5>Search page Under Construction</h5>
            }/>
            <Route exact path='/about' render={() =>
            <h5>About page Under Construction</h5>
            }/>
            <Route exact path='/user/:id' render={() =>
              <User data={this.state.data}/>
            }/>
            <Route exact path='/user/:id/:postid' render={() =>
              <PostLoader data={this.state.data}/>
            }/>
        </main>
        <Footer />
      </div>
    )
  }
}
export default App;
