import {Component} from 'react'
import { Link } from 'react-router-dom'
import '../styles/profile.css'
import * as ux from '../utils/uxFunctions'

class Profile extends Component{
    state = {
        imgs: [],
        user: {}
    }
    componentDidMount(){
        ux.startLoading("profile-posts");
        const top = document.getElementById("top") 
        top.scrollIntoView()
        top.remove()
        const loading = document.getElementById("loading")
        if(loading){
        loading.remove();
    }}
    render(){
        const user = this.props.user
        const arr = this.props.posts
        const postlink = `/user/${user.username}/`
        return(
            <div className="profile-body">
                <div id="top">             
                </div>
                <div className="profile-header">
                    <div className="profile-picture" style={{backgroundImage: `url(${user.displayPic})`}}></div>
                    <div className="profile-info">
                        <h1>{user.username}</h1>
                        <h2>{user.description}</h2>                    
                    </div>                    
                </div>
                <h3 className="profile-posts-title" id="profile-posts">Posts</h3>
                <div className="profile-posts" >
                   {arr.map((item) => {
                        return(
                            <a>
                                {item.type === "video" ? 
                                <div className="profile-post-item"  ><iframe src={item.url}></iframe></div>:
                                <Link to={postlink+item.id}><div className="profile-post-item" style={{backgroundImage: `url(${item.url})`}}></div></Link>
                                }
                            </a>
                        )
                    })}
                </div>
            </div>
        )}
}
export default Profile